import axios from "axios";
import { activitiesApi } from "../api";
import { ACTIVITIES_LOADING, GET_ACTIVITIES } from "./Types";

export const getActivities = (pageNo) => async (dispatch) => {
	dispatch({ type: ACTIVITIES_LOADING });

	let page;
	if (pageNo === 1) page = 1;
	else {
		if ((pageNo - 1) % 3 === 0) page = 1;
		else if ((pageNo - 1) % 3 === 2) page = 2;
		else page = 3;
	}

	let ind;
	if (pageNo === 1) ind = 3;
	else if ((pageNo - 1) % 3 === 0) ind = 3 - Math.floor((pageNo - 1) / 3);
	else ind = 3 - Math.floor((pageNo - 1) / 3) - 1;

	try {
		let res = await axios.get(activitiesApi[ind], {
			params: {
				_page: page,
				_limit: 10,
			},
		});

		dispatch({ type: GET_ACTIVITIES, payload: res.data });
	} catch (err) {
		if (err.response) console.log(err.response);
	}
};
