import { ACTIVITIES_LOADING, GET_ACTIVITIES } from "../actions/Types";

const initialState = {
	activities: [],
	loading: false,
};

const reducer = (state = initialState, action) => {
	switch (action.type) {
		case GET_ACTIVITIES:
			return {
				...state,
				activities: [...state.activities, ...action.payload],
				loading: false,
			};

		case ACTIVITIES_LOADING:
			return { ...state, loading: true };

		default:
			return state;
	}
};

export default reducer;
