import React, { useState, createRef } from "react";
import Dropdown from "../Dropdown";
import SearchTag from "./searchTag";
import Tooltip from "../Tooltip";
import { connect } from "react-redux";
import {
	getSuggestions,
	deletePlace,
	addPlace,
} from "../../actions/PlacesActions";
import PropTypes from "prop-types";

import "./searchbar.scss";

const SearchBar = ({
	getSuggestions,
	deletePlace,
	addPlace,
	places,
	suggestions,
	parentRef,
}) => {
	const [search, changeSearch] = useState("");
	const [show, toggle] = useState(false);
	const [tooltipShow, toggleTooltip] = useState(false);
	const ref = createRef(null);

	// useEffect(() => {
	// 	document.addEventListener("keypress", handleOutsideClick);
	// 	return () => document.removeEventListener("keypress", handleOutsideClick);
	// });

	// const handleOutsideClick = (e) => {
	// 	// console.log("handle outside click ", ref, ref.current.contains(e.target));
	// 	if (ref && ref.current && !ref.current.contains(e.target)) {
	// 		alert("Outside click");
	// 		toggle(false);
	// 	}
	// };

	const handleChange = (e) => {
		changeSearch(e.target.value);
		if (e.target.value.trim().length > 0) toggle(true);
		else toggle(false);
		getSuggestions(e.target.value);
	};

	const placesInContainer =
		places.length > 2 ? [...places].slice(places.length - 2) : [...places];

	return (
		<div className="searchbar" ref={ref}>
			<div className="wrapper">
				{places.length > 0 && (
					<div className="tagContainer">
						{placesInContainer.slice().map((place, i) => (
							<SearchTag key={i} place={place} click={deletePlace} />
						))}
					</div>
				)}
				<input type="text" value={search} onChange={handleChange} />
				{show && suggestions && (
					<Dropdown
						list={suggestions}
						places={places}
						click={addPlace}
						search={search}
					/>
				)}
				<div
					className="icon"
					style={{
						minWidth: search.trim().length > 0 ? "1.25rem" : "1.6rem",
						height: search.trim().length > 0 ? "1.25rem" : "1.6rem",
					}}
					onClick={(e) => {
						if (search.trim().length === 0) e.preventDefault();
						else {
							addPlace({
								id: Math.floor(Number.MAX_SAFE_INTEGER * Math.random()),
								name: search,
							});
							changeSearch("");
						}
					}}
					onMouseEnter={() => toggleTooltip(true)}
					onMouseLeave={() => toggleTooltip(false)}
				>
					<img
						src={
							search.trim().length === 0
								? require("../../icons/search.png").default
								: require("../../icons/cross.png").default
						}
						alt="search"
						style={{
							transform:
								search.trim().length > 0 ? "rotateZ(45deg)" : "rotate(0)",
						}}
					/>
					{tooltipShow && (
						<Tooltip text={search.trim().length === 0 ? "Search" : "Add New"} />
					)}
				</div>
			</div>
		</div>
	);
};

SearchBar.propTypes = {
	getSuggestions: PropTypes.func.isRequired,
	addPlace: PropTypes.func.isRequired,
	deletePlace: PropTypes.func.isRequired,
};

const mapStateToProps = ({ places }) => ({
	suggestions: places.suggestions,
	places: places.places,
	loading: places.loading,
});

export default connect(mapStateToProps, {
	getSuggestions,
	deletePlace,
	addPlace,
})(SearchBar);
