import React, { useState, createRef, useEffect } from "react";

import "./dropdown.scss";

const Dropdown = ({ list, click, search }) => {
	const [focused, changeFocus] = useState(-1);
	const ref = createRef(null);

	/* eslint-disable */
	useEffect(() => {
		if (ref.current) ref.current.focus();
	}, [focused]);
	/* eslint-enable */

	return (
		<div className="dropdown" tabIndex={1}>
			{list.map((item, i) => {
				let name = item.name.toLowerCase();
				let idx = name.indexOf(search);
				let n = search.length;

				return (
					<button
						className={`item ${focused === i ? "active" : ""}`}
						key={i}
						onKeyDown={(e) => {
							e.preventDefault();
							if (e.key === "ArrowDown" && focused < list.length - 1)
								changeFocus(focused + 1);
							else if (e.key === "ArrowUp" && focused >= 1)
								changeFocus(focused - 1);
							else if (e.key === "Enter") click(item);
						}}
						ref={focused === i ? ref : null}
						onClick={() => {
							changeFocus(i);
							click(item);
						}}
						tabIndex={1}
					>
						{name.substr(0, idx)}
						<span className="highlight">{name.substr(idx, n)}</span>
						{name.substr(idx + n)}
					</button>
				);
			})}
		</div>
	);
};

export default Dropdown;
