import {
	PLACES_LOADING,
	GET_PLACES,
	ADD_PLACE,
	DELETE_PLACE,
	SUGGESTION_LOADING,
	GET_SUGGESTION_PLACES,
} from "../actions/Types";

const initialState = {
	places: [],
	suggestions: null,
	loading: false,
};

const reducer = (state = initialState, action) => {
	switch (action.type) {
		case GET_PLACES:
			return { ...state, places: action.payload, loading: false };

		case ADD_PLACE:
			return {
				...state,
				places: [...new Set([...state.places, action.payload])],
			};

		case DELETE_PLACE:
			return {
				...state,
				places: state.places.filter((place) => place.id !== action.payload),
			};

		case GET_SUGGESTION_PLACES:
			return {
				...state,
				suggestions: action.payload,
				loading: false,
			};

		case PLACES_LOADING:
			return { ...state, loading: true };

		case SUGGESTION_LOADING:
			return { ...state, loading: true };

		default:
			return state;
	}
};

export default reducer;
