import React, { useState } from "react";
import Tooltip from "../Tooltip";

const Item = ({ item, click, size, deleteIcon, type, itemRef }) => {
	const [show, toggle] = useState(false);

	return (
		<div className="item" style={{ fontSize: size }} ref={itemRef}>
			<span>{item[type]}</span>
			<div
				className="icon"
				onClick={() => click(item)}
				onMouseEnter={() => toggle(true)}
				onMouseLeave={() => toggle(false)}
			>
				{deleteIcon && (
					<img src={require("../../icons/cross.png").default} alt="cross" />
				)}
				{deleteIcon && show && <Tooltip text="Delete" />}
			</div>
		</div>
	);
};

export default Item;
