import React, { Component } from "react";
import Content from "./Components";
import { Provider } from "react-redux";
import store from "./store";

import "./App.scss";

class App extends Component {
	render() {
		return (
			<Provider store={store}>
				<div className="App">
					<Content />
				</div>
			</Provider>
		);
	}
}

export default App;
