import React, { useEffect, useState, useRef, useCallback } from "react";
import Loader from "../Loader";
import { connect } from "react-redux";
import { getActivities } from "../../actions/ActivitiesActions";
import PropTypes from "prop-types";
import Item from "./item";

const RightView = ({ activities, getActivities, loading }) => {
	const [page, changePage] = useState(1);
	const observer = useRef();
	const lastElementCallback = useCallback(
		(node) => {
			if (loading) return;

			if (observer.current) observer.current.disconnect();

			observer.current = new IntersectionObserver((entries) => {
				if (entries[0].isIntersecting && activities.length < 100) {
					changePage(page + 1);
				}
			});
			if (node) observer.current.observe(node);
		},
		[page, activities, loading]
	);

	/* eslint-disable */
	useEffect(() => {
		getActivities(page);
	}, [page]);
	/* eslint-enable */

	return (
		<div className="rightView">
			{activities.map((activity, i) => (
				<Item
					key={i}
					itemRef={i === activities.length - 1 ? lastElementCallback : null}
					size="0.95rem"
					item={activity}
					type="activity"
				/>
			))}
			{loading && <Loader />}
		</div>
	);
};

RightView.propTypes = {
	getActivities: PropTypes.func.isRequired,
};

const mapStateToProps = ({ activities }) => ({
	activities: activities.activities,
	loading: activities.loading,
});

export default connect(mapStateToProps, {
	getActivities,
})(RightView);
