import { cities } from "../city.json";
import {
	PLACES_LOADING,
	GET_PLACES,
	ADD_PLACE,
	DELETE_PLACE,
	SUGGESTION_LOADING,
	GET_SUGGESTION_PLACES,
} from "./Types";

export const getPlaces = () => async (dispatch) => {
	dispatch({ type: PLACES_LOADING });

	const res = [];
	for (let i = 0; i < 5; i++)
		res.push(cities[Math.floor(Math.random() * cities.length)]);

	dispatch({
		type: GET_PLACES,
		payload: [...res],
	});
};

export const addPlace = (place) => (dispatch) => {
	dispatch({
		type: ADD_PLACE,
		payload: place,
	});
};

export const deletePlace = (place) => (dispatch) => {
	dispatch({ type: DELETE_PLACE, payload: place.id });
};

export const getSuggestions = (search) => async (dispatch) => {
	dispatch({ type: SUGGESTION_LOADING });

	let res;

	if (!search || search.trim().length === 0) res = null;
	else
		res = [...cities].filter((city) =>
			city.name.toLowerCase().includes(search)
		);

	dispatch({
		type: GET_SUGGESTION_PLACES,
		payload: res,
	});
};
